import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
// import { ExternalSystems } from '../external-system/externalsystem';
// import {ProcessServiceModule} from '../external-system/processServiceModule';
import { retry, catchError } from 'rxjs/operators';
import { StringMap } from '@angular/compiler/src/compiler_facade_interface';
import { map } from 'rxjs/operators';
import { NewExternalSystems } from './externalsystem';
import { ProcessServiceModule } from './processServiceModule';
import { environment } from 'src/environments/environment.prod';

@Injectable({
    providedIn: 'root'
})
export class ExternalSystemService {
    _baseURL: any;
    // AppConstants.ba
    private newUrl = environment.MEDIAN_URL;

    // private newUrl = 'http://localhost:9800/externalSystem';
    // private newUrlforcodes = 'http://localhost:9800/processServiceModule';

    constructor(private http: HttpClient) {
        this._baseURL = this.newUrl;
        console.log(this._baseURL);
    }

    handleError(error: HttpErrorResponse) {
        if (error.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', error.error.message);
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error(
                `Backend returned code ${error.status}, ` +
                `body was: ${error.error}`);
        }
        // return an observable with a user-facing error message
        return throwError(
            'Something bad happened; please try again later.');
    };

    getExternalSystemList(): Observable<NewExternalSystems> {
        return this.http.get<any>(`${this._baseURL}/externalSystem/getAllExternalSystems`);
        //`${API_URL}/users/getModifiedUser/
        //   .pipe(retry(2), catchError(this.handleError))
    }

    // saveNewExternalSystem(newExternalSys: ExternalSystems): Observable<ExternalSystems> {  
    //     const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json'}) };  
    //     return this.http.post<ExternalSystems>(this.newUrl + '/save/',  newExternalSys, httpOptions);  
    // }

    saveNewExternalSystem(formData: FormData): Observable<any> {
        console.log("Reached service.ts page");
        console.log("formData is :" + formData);
        return this.http.post(`${this._baseURL}/externalSystem/save/`, formData);
    }

    getExternalSystemById(externalSystemCode: string): Observable<NewExternalSystems> {
        return this.http.get<NewExternalSystems>(`${this._baseURL}/externalSystem/getExternalSystemById/` + externalSystemCode);
    }

    updateExternalSystem(updateExternalSys: NewExternalSystems): Observable<NewExternalSystems> {
        const httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) };
        return this.http.put<NewExternalSystems>(`${this._baseURL}/update/`, updateExternalSys, httpOptions);
    }

    deleteExternalSysById(externalSystemCode: string): Observable<any> {
        console.log("Reached service page");
        return this.http.delete<any>(`${this._baseURL}/externalSystem/delete/` + externalSystemCode);
    }

    authorized(externalSysIdUpdate: string, creatorId: string) {
        console.log(externalSysIdUpdate);
        console.log(creatorId);
        return this.http.get<NewExternalSystems>(`${this._baseURL}/externalSystem/authorize/` + externalSysIdUpdate + '/' + creatorId)
    }

    downloadxsd(filename: any, extension: any): Observable<HttpResponse<string>> {
        let headers = new HttpHeaders();
        headers = headers.append('Accept', 'text/xsd; charset=utf-8')

        return this.http.get(this._baseURL + '/externalSystem/downloadfile/' + filename + "/" + extension, {
            headers: headers,
            observe: 'response',
            responseType: 'text'
        });
    }
    downloaddtd(filename: any, extension: any): Observable<HttpResponse<string>> {
        let headers = new HttpHeaders();
        headers = headers.append('Accept', 'text/xsd; charset=utf-8')

        return this.http.get(this._baseURL + '/externalSystem/downloadfile/' + filename + "/" + extension, {
            headers: headers,
            observe: 'response',
            responseType: 'text'
        });
    }


    getProcessServiceModule(): Observable<ProcessServiceModule> {
        return this.http.get<ProcessServiceModule>(this._baseURL + '/processServiceModule/getAllProcessServiceModule')
            .pipe(retry(2), catchError(this.handleError))
    }

    getprocessCode(moduleCode: string): Observable<any> {
        return this.http.get<any>(this._baseURL + '/processServiceModule/getprocessCode/' + moduleCode)
            .pipe(retry(2), catchError(this.handleError))
    }

}