import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { ExternalSystemService } from '../external-system.service';
import { Observable } from 'rxjs';
import { NewExternalSystems } from '../externalsystem';
import { DataSource } from '@angular/cdk/table';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import * as fileSaver from 'file-saver';



@Component({
  selector: 'app-external-system-summary',
  templateUrl: './external-system-summary.component.html',
  styleUrls: ['./external-system-summary.component.css']
})

export class ExternalSystemSummaryComponent implements OnInit {

  searchField;

  AllowDownload: boolean = false;

  matButtonToggleGroup;

  public DownloadType: string;

  public selectedVal: string = '';

  idToEnable: string;

  displayedColumns: string[] = ['externalSystemCode', 'externalSystemName', 'processCodes', 'creatorId', 'createdAt'
    , 'verifierId', 'verifiedAt', 'recordStatusText', 'xsdfile', 'dtdfile'];
  dataSource = new MatTableDataSource();

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  constructor(public router: Router, private formBuilder: FormBuilder, private externalsysService: ExternalSystemService) { }

  ngOnInit() {
    console.log('component summary loaded.')
    this.dataSource.paginator = this.paginator;
    this.loadAllData();
    this.AllowDownload = false;
    this.selectedVal = '';
    this.dataSource.filterPredicate = function (data: NewExternalSystems, filter: string): boolean {
      return data.externalSystemCode.toLowerCase().includes(filter);
    };
  }

  loadAllData() {
    this.externalsysService.getExternalSystemList().subscribe((response: any) => {
      this.dataSource.data = response;
      console.log('data for ext sys' + this.dataSource.data)
    });
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  clearSearchField() {
    this.searchField = '';
    this.dataSource.filter = '';
  }

  public onValChange(val: string, id: string) {
    this.selectedVal = val;
    this.idToEnable = id;
    this.AllowDownload = true;
  }

  DisableOrNot(checkId?: string) {
    if (checkId == this.idToEnable && this.AllowDownload == true) {
      return false;
    }
    else {
      return true;
    }
  }


  download(element) {
    console.log(this.selectedVal)
    console.log(element);
    console.log("download");
    if (this.selectedVal == 'xsd') {
      this.externalsysService.downloadxsd(element, this.selectedVal).subscribe(response => {
        console.log(response.body);
        if (response.body != "") {
          console.log(response);
          //const filename=response.headers.get('filename');
          const filename = element;
          console.log(filename);
          //console.log(filename);
          // filename='ass';
          this.saveFilexsd(response.body, filename + '.xsd');
        } else {
          Swal.fire("File is not Available for this External System");
        }
      })

    } else if (this.selectedVal == 'dtd') {
      this.externalsysService.downloaddtd(element, this.selectedVal).subscribe(response => {

        console.log(response);
        if (response.body != "") {
          //const filename=response.headers.get('filename');
          const filename = element;
          console.log(filename);
          // filename='ass';
          this.saveFiledtd(response.body, filename + '.dtd');
        } else {
          Swal.fire("File is not Available for this External System");
        }
      })
    }
  }

  saveFilexsd(data: any, filename?: string) {
    const blob = new Blob([data], { type: 'text/xsd; charset=utf-8' });
    fileSaver.saveAs(blob, filename);
  }
  saveFiledtd(data: any, filename?: string) {
    const blob = new Blob([data], { type: 'text/dtd; charset=utf-8' });
    fileSaver.saveAs(blob, filename);
  }


}



